import React from "react";
import { Temperature } from "./Temperature";

export function TempTsPair(
  { ts, temp, includeMinutes } = { includeMinutes: false }
) {
  return (
    <div>
      <span className="time">
        klo {ts.getHours()}
        {includeMinutes ? ":" + ts.getMinutes() : ""}{" "}
      </span>
      <Temperature temp={Math.round(temp)}></Temperature>
    </div>
  );
}
