import React from "react";
import { isToday, isTomorrow, format } from "date-fns";

export function Timestamp({ ts }) {
  // console.log(typeof ts);

  return (
    <>
      {/* {ts.toLocaleString("fi-FI")} - */}
      {format(ts, "EEEE - ")}
      {isToday(ts)
        ? "T채n채채n"
        : isTomorrow(ts)
        ? "Huomenna"
        : format(ts, "d.MM.")}
    </>
  );
}
